import HistoryThumbnail from 'assets/img/history.png'
import ThreeDLeft from 'assets/img/3d-left.png'
import ThreeDRight from 'assets/img/3d-right.png'

export const GRAPHQL_URL = process.env.REACT_APP_GRAPHQL_URL!
export const SUBSCRIPTION_URL = process.env.REACT_APP_SUBSCRIPTION_URL!
export const API_URL = process.env.REACT_APP_API_URL!
export const FILES_URL = process.env.REACT_APP_FILES_URL!

export const ROUTES = {
  home: {
    url: '/',
    params: '',
  },
  detail: {
    url: '/detail',
    params: '?studyIds=:studyIds&patientId=:patientId&imageIds=:imageIds',
  },
  importer: {
    url: '/importer',
    params: '',
  },
}

const subject = new Subject<{ topic: string; data?: any }>()

export const globalSubject = {
  subscribe: (_topic: string, callback: Function) => {
    return subject.subscribe({
      next: ({ topic, data }) => {
        if (topic === _topic) {
          callback(data)
        }
      },
    })
  },
  next: (topic: string, data?: any) => {
    subject.next({ topic, data })
  },
}

export enum SubjectTopic {
  InitXrayTool = 'initXrayTool',
  SwitchFunc = 'switchFunc',
}

export type Layout = '1x1' | '1x2' | '2x1' | '2x2' | 'pa/pa' | 'pa/lat'

export const GRIDS: Record<Layout, number> = {
  '1x1': 1,
  '1x2': 2,
  '2x1': 2,
  '2x2': 4,
  // Hanging protocol
  'pa/pa': 2,
  'pa/lat': 2,
}

export const DEFAULT_LAYOUT: Layout = '2x2'

export const parseImageItem = (item: DicomImage | null, series_inst_uid: string): DraggableItem => {
  if (!item) {
    // Empty item
    return {
      id: Math.random().toFixed(3).toString(),
      func: 'dicom',
      file: '',
      thumbnail: '',
      laterality: 'left', // todo: get from db
      plugin: [],
      dicomToolType: null,
      pluginType: null,
      series_inst_uid,
    }
  }

  const { sop_inst_uid, func, file, thumbnail, plugin } = item as DicomImage

  return {
    id: `${func}-${sop_inst_uid}`,
    func,
    file: `${FILES_URL}/${file}`,
    thumbnail: `${FILES_URL}/${thumbnail}`,
    laterality: 'left', // todo: get from db
    plugin,
    dicomToolType: null,
    pluginType: null,
    series_inst_uid,
  }
}

export const parseInferenceItem = (item: Inference, series_inst_uid: string): DraggableItem => {
  const { id, func } = item
  let file = ''
  let thumbnail = ''
  let data
  let laterality

  switch (item.func) {
    case '3d':
      const result = item.result as ThreeDResult
      laterality = result.laterality
      file = `${FILES_URL}/${result.file}`
      thumbnail = laterality === 'left' ? ThreeDLeft : ThreeDRight
      break
    case 'oa':
      thumbnail = SummaryThumbnail
      data = item.result as OAResult
      break
    case 'history':
      thumbnail = HistoryThumbnail
      break
  }

  return {
    id: `${func}-${id}`,
    func,
    file,
    thumbnail,
    data,
    laterality,
    plugin: [],
    dicomToolType: null,
    pluginType: 'ai',
    series_inst_uid,
  }
}

export const parseStudiesByYear = (studies: Study[]): Map<string, Study[]> => {
  const newStudiesByYear = new Map()
  studies.forEach((study) => {
    const year = parseInt(study.created_at.substr(0, 4), 10) - 1911
    let studyGroup = newStudiesByYear.get(year)
    if (studyGroup) {
      studyGroup.push(study)
    } else {
      studyGroup = [study]
    }
    newStudiesByYear.set(year, studyGroup)
  })

  return newStudiesByYear
}

export const parseHangingProtocolItems = (
  items: Prediction[],
  series_inst_uid: string,
  protocol: Protocols,
  laterality: Laterality
): DraggableItem[] => {
  let firstItem
  let secondItem
  switch (protocol) {
    case 'pa/pa':
      firstItem = items.find((item) => item.laterality === 'L' && item.view_position === 'PA')!
      secondItem = items.find((item) => item.laterality === 'R' && item.view_position === 'PA')!
      break
    case 'pa/lat':
      firstItem = items.find(
        (item) => item.laterality === laterality && item.view_position === 'PA'
      )!
      secondItem = items.find((item) => item.view_position === `${laterality}L`)!
      break
    default:
      // TODO: user defined hp
      return []
  }

  // PA or LAT doesn't exist
  if (!firstItem || !secondItem) {
    return []
  }

  return [
    {
      id: `prediction-${firstItem.laterality}-${firstItem.view_position}`,
      func: 'hanging-protocol',
      file: `${FILES_URL}/${firstItem.file}`,
      thumbnail: '',
      laterality: firstItem.laterality,
      plugin: [],
      dicomToolType: null,
      pluginType: null,
      series_inst_uid,
    },
    {
      id: `prediction-${secondItem.laterality}-${secondItem.view_position}`,
      func: 'hanging-protocol',
      file: `${FILES_URL}/${secondItem.file}`,
      thumbnail: '',
      laterality: secondItem.laterality,
      plugin: [],
      dicomToolType: null,
      pluginType: null,
      series_inst_uid,
    },
  ]
}
