import React, { Suspense } from 'react'
import { ApolloProvider } from '@apollo/client'
import { BrowserRouter, Route, Switch } from 'react-router-dom'
import utc from 'dayjs/plugin/utc'
import dayjs from 'dayjs'
import { client } from 'common/apolloClient'
import { ROUTES } from 'common/utils'

dayjs.extend(utc)

const PatientList = React.lazy(() => import('./PatientList/PatientList'))
const PatientDetailContainer = React.lazy(() => import('./PatientDetail/PatientDetailContainer'))
const DicomImporter = React.lazy(() => import('./DicomImporter/DicomImporter'))

function App() {
  return (
    <div
      style={{
        fontFamily: 'Noto Sans, sans-serif',
        height: '100vh',
        background: '#364349',
        overflow: 'hidden',
      }}
    >
      <ApolloProvider client={client}>
        <BrowserRouter>
          <Suspense fallback={<div>Loading...</div>}>
            <Switch>
              <Route path={ROUTES.detail.url} exact={true}>
                <PatientDetailContainer />
              </Route>
              <Route path={ROUTES.importer.url} exact={true}>
                <DicomImporter />
              </Route>
              <Route path={ROUTES.home.url} exact={true}>
                <PatientList />
              </Route>
            </Switch>
          </Suspense>
        </BrowserRouter>
      </ApolloProvider>
    </div>
  )
}

export default App
